import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

function fixUpper(string: string) {
  let firstChar = string.charAt(0)
  const restOfString = string.substring(1)
  firstChar = firstChar.toUpperCase()
  return firstChar + restOfString
}

function fixFilename(fileName: string) {
  return fixUpper(fileName.replace(/^\.\/(.*)\.\w+$/, '$1'))
}

const requireComponent = require.context(
  './components', // The relative path of the components folder
  true, // Whether or not to look in subfolders
  /[A-Za-z]\w+\.vue$/ // The regular expression used to match base component filenames
)
requireComponent.keys().forEach(fileName => {
  const componentConfig = requireComponent(fileName)
  const componentName = fixFilename(fileName)
  Vue.component(
    componentName,
    componentConfig.default || componentConfig
  )
})

new Vue({
  render: h => h(App),
}).$mount('#app')
