










import { Component, Vue } from 'vue-property-decorator';
@Component({})
export default class RadiationCollector extends Vue {
    decayFactor = 700
    shotsInput = 0
    tickRate = 2 //seconds

    get powerLevel() {
        return this.shotsInput*14
    }
    get unshieldedDistance() {
        return Math.round(Math.sqrt(this.powerLevel/2))
    }
    get shieldedDistance() {
        return Math.round(this.unshieldedDistance*2/3)
    }

    get unroundedShieldValue() {
        return Math.sqrt(this.powerLevel/2)*2/3
    }

    get halfRoundedShieldValue() {
        return Math.round(Math.sqrt(this.powerLevel/2))*2/3
    }

    get halfLife() {
        if (this.powerLevel<=0) return 0
        let counter = 0
        let current = this.powerLevel
        const target = this.powerLevel/2
        while (current>target && current>0) {
            current -= this.radTick(current)
            counter++
            if (counter>500000) return "A long time"
        }
        return this.roundToPointOne((counter*this.tickRate)/60) + " minutes"
    }

    radTick(val: number) {
        if (val==0) return val
        return Math.pow((val/this.decayFactor), 3)
    }

    roundToPointOne(num: number) {
        return (Math.round(num*10))/10
    }

}
