





import { Component, Vue, Prop, Watch, Emit} from 'vue-property-decorator';
@Component({})
export default class Examples extends Vue {
    someData: string = "aa".split('a')[0];

    @Prop({ default: 'exampleProp' })
    exampleProp!: string;

    get computedProp() {
        return 8
    }

    someMethod(str: string) {
        console.log(str);
        this.someData = "aaa"
    }

    @Watch('someData')
    onItDidTheThing(value: string, oldValue: string) {
        
    }

    @Emit() //Should emit event of emit-the-thing: 69, e
    emitTheThing(e: Event) {
        return 69;
    }

    @Emit('input') //Should emit event of input: event.target, ev
    emitTheOtherThing(ev: Event) {
        return ev?.target;
    }

}
